@media screen and (max-width: 768px) {
    form {
        -ms-flex-pack: distribute;
            justify-content: space-around;
    }
}

form {
    width: 100%;
    margin-bottom: 20px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
}

input[type="search"] {
    width: 300px;
    height: 30px;
    font-size: 13px;
    padding-left: 8px;
    padding-right: 8px;
    white-space: nowrap;
    vertical-align: middle;
    border: none;
    border-radius: 3px;
    color: rgba(255, 255, 255, 0.75);
    background-color: rgba(255, 255, 255, 0.125);
    margin-right: 10px;
}

input[type="search"]:hover, input[type="search"]:focus {
    outline: none;
    border: none;
}

button[type="submit"] {
    color: #24292e;
    background-color: #eff3f6;
    background-image: -webkit-gradient(linear, left top, left bottom, from(#fafbfc), color-stop(90%, #eff3f6));
    background-image: -webkit-linear-gradient(top, #fafbfc 0%, #eff3f6 90%);
    background-image: -o-linear-gradient(top, #fafbfc 0%, #eff3f6 90%);
    background-image: linear-gradient(-180deg, #fafbfc 0%, #eff3f6 90%);
    border: 1px solid rgba(27, 31, 35, 0.2);
    border-radius: 3px;
    white-space: nowrap;
    cursor: pointer;
    height: 30px;
    vertical-align: middle;
    padding-left: 8px;
    padding-right: 8px;
    font-weight: 600;
}

button[type="submit"]:hover {
    color: #24292e;
    background-color: #e6ebf1;
    background-image: -webkit-gradient(linear, left top, left bottom, from(#f0f3f6), color-stop(90%, #e6ebf1));
    background-image: -webkit-linear-gradient(top, #f0f3f6 0%, #e6ebf1 90%);
    background-image: -o-linear-gradient(top, #f0f3f6 0%, #e6ebf1 90%);
    background-image: linear-gradient(-180deg, #f0f3f6 0%, #e6ebf1 90%);
    border: 1px solid rgba(27, 31, 35, 0.2);
}



